import { ActorBase, ActorStates } from "./actor-base"
import { AnimationHelper } from "../tools"
import { IResourceDictionary } from "pixi.js"

/**
 *
 */
export class EnemyBase extends ActorBase {
  constructor(
    resources: IResourceDictionary,
    skin: string,
    speed: number = 2
  ) {
    super(resources, skin, speed)

    this._isFacingRight = false

    // load the clips for this object
    this._clips[
      this._skin + ".walk"
    ] = AnimationHelper.loadClip(
      resources,
      skin,
      "walk",
      0.2,
      1
    )
    this._clips[this._skin + ".gothit"] = this._clips[
      this._skin + ".walk"
    ]
    this._clips[this._skin + ".died"] = this._clips[
      this._skin + ".walk"
    ]

    // go to idle
    this._idle()
  }

  /**
   *
   * @private
   */
  protected _idle() {
    this._setState(ActorStates.idle)
    this._playClip(this._skin + ".walk", true)
  }

  /**
   *
   * @private
   */
  protected _died() {
    super._died()

    // set proper death location
    this.y += this.height / 2
    this.pivot.set(Math.abs(this.width) / 2, this.height)
    this.rotation = (90 * Math.PI) / 180

    setTimeout(() => {
      this.parent.removeChild(this)
    }, 10 * 1000)
  }

  move(maxHeight: number, maxWidth: number): boolean {
    let enemyMoved = false

    if (this.vx !== 0 || this.vy !== 0 || this.vr !== 0) {
      this.x += this.vx
      this.y += this.vy
      this.rotation += this.vr

      enemyMoved = true

      this.adaptScale(this.y, maxHeight)
    }

    return enemyMoved
  }
}
