import { Door } from "../objects"
import { Config } from "../config"
import {
  Container,
  IResourceDictionary,
  Sprite,
  Texture
} from "pixi.js"
import { MapBase } from "../../engine"

/**
 *
 */
export class Loading extends MapBase {
  private readonly _door: Door

  constructor(
    resources: IResourceDictionary,
    stage: Container,
    done?: () => void
  ) {
    super(resources, stage)

    const bg = new Sprite(Texture.WHITE)

    this._door = new Door(resources, done)
    this._door.x = Config.constants.width / 2
    this._door.y = 30

    bg.width = Config.constants.width
    bg.height = 800
    bg.tint = Config.constants.black

    this._backgroundContainer.addChild(bg)

    this._setup()
  }

  /**
   *
   * @private
   */
  protected _setup() {
    this._foregroundContainer.addChild(this._door)
  }

  public tick(delta: number) {
    this._door.y += delta / 100

    const scale = this._door.scale.x + delta / 20000
    this._door.scale.set(scale)
  }
}
