import { WeaponBase } from "../../../engine"
import { Muzzle } from "./accessory/muzzle"
import { Shell } from "./accessory/shell"

export default class Weapon extends WeaponBase {
  fire(): boolean {
    const fired = super.fire()

    if (fired) {
      // muzzle flash
      const muzzleFlash = new Muzzle(this._resources, 2)

      muzzleFlash.x = 48
      muzzleFlash.y = -25
      this.addChild(muzzleFlash)

      const shell = new Shell(
        this._resources,
        this._roundsSprite?.texture!
      )

      const p = this.parent.parent
      shell.position = this.parent.position

      // add shell to the parent element because we want to let it fly around
      p.addChild(shell)

      this._shellSprites.push(shell)

      setTimeout(() => {
        p.removeChild(shell)
        this._shellSprites = this._shellSprites.splice(
          this._shellSprites.findIndex((s) => s === shell)
        )
      }, 3 * 1000)
    }

    return fired
  }
}
