import { IResourceDictionary } from "pixi.js"
import {
  AnimationBase,
  AnimationHelper
} from "../../../../engine"

/**
 *
 */
export class Muzzle extends AnimationBase {
  constructor(
    resources: IResourceDictionary,
    speed: number = 0.01
  ) {
    super(resources)
    const sheet = "weapons.effects"

    this._clips = {
      [sheet]: AnimationHelper.loadClip(
        this._resources,
        sheet,
        "muzzle",
        speed,
        1
      )
    }

    this._playClip(sheet, false, false)
  }
}
