import { IResourceDictionary, Sprite } from "pixi.js"
import { AnimationBase } from "../../../engine"

/**
 *
 */
export class Impact extends AnimationBase {
  constructor(resources: IResourceDictionary) {
    super(resources)

    const resource = this._resources["weapons.effects"]
    const impact = new Sprite(
      resource.textures!["impact.png"]
    )

    this.addChild(impact)
  }
}
