import { IResourceDictionary, Text } from "pixi.js"
import { Config } from "../../game/config"
import { ObjectBase } from "../objects"
import { PlayerBase } from "../actors"

export class HUDBase extends ObjectBase {
  // change to "you are dead ..."
  protected _gameOverSign?: Text
  protected _player: PlayerBase

  constructor(resources: IResourceDictionary) {
    super(resources)

    this.zIndex = 2

    this._player = new PlayerBase(resources, "none", 0)
  }

  public update() {}

  public renderGameOver() {
    this._gameOverSign = new Text(
      "Game Over\nPress Enter to retry!",
      {
        ...Config.styling.bigText,
        fill: "#f00"
      }
    )

    this.addChild(this._gameOverSign)

    // place game over sign in center of screen
    this._gameOverSign.x =
      Config.constants.width / 2 -
      this._gameOverSign.width / 2
    this._gameOverSign.y =
      Config.constants.height / 2 -
      this._gameOverSign.height / 2
  }
}
