import { IResourceDictionary, Sprite } from "pixi.js"
import Weapon from "./weapon"

/**
 *
 */
export class Shotgun extends Weapon {
  /**
   *
   * @param resources
   */
  constructor(resources: IResourceDictionary) {
    super(resources, 55, 6)

    const resource = resources!["weapons.shotgun"]

    this._roundsSprite = new Sprite(
      resource.textures!["shotgun_shell.png"]!
    )

    this._sprite = new Sprite(
      resource.textures!["shotgun.png"]!
    )

    this._sprite.scale.x = 0.3
    this._sprite.scale.y = 0.3

    // shotgun should be in the back so add it first
    this.addChild(this._sprite)
  }
}
