import { Container, IResourceDictionary } from "pixi.js"
import { Gamepads } from "./input"
import { MapBase } from "./maps"
import { HUDBase } from "./ui"
import { PlayerBase } from "./actors"

export class GameBase {
  protected _resources: IResourceDictionary
  protected _stage: Container

  protected _isRunning: boolean = false
  // current map
  protected _map?: MapBase
  // Heads up display
  protected _hud?: HUDBase
  // player
  protected _player?: PlayerBase

  protected _elapsedSeconds = 0
  protected _startOffset?: number

  constructor(
    resources: IResourceDictionary,
    stage: Container
  ) {
    this._resources = resources
    this._stage = stage

    this._map = new MapBase(resources, stage)
    this._hud = new HUDBase(resources)
    this._player = new PlayerBase(resources, "none", 0)
  }

  public tick(elapsed: number, delta: number) {
    Gamepads.updateStatus()
  }

  protected _init() {
    // attach the keyboard to player
    const hasGamepad = this._attachGamepad()

    // no gamepad, go to stinky keyboard
    if (!hasGamepad) {
      console.log("no gamepad, attaching keyboard")
      this._attachKeyboard()
    }

    // this game is running baby
    this._isRunning = true
  }

  /**
   * this will end the game
   * @private
   */
  protected _end() {
    // game is not longer running dude
    this._isRunning = false

    this._hud!.renderGameOver()
  }

  protected _attachKeyboard() {
    return false
  }

  protected _attachGamepad(): boolean {
    return false
  }
}
