import {
  BLEND_MODES,
  filters,
  IResourceDictionary,
  Sprite,
  Texture
} from "pixi.js"
import { SimpleLightmapFilter } from "@pixi/filter-simple-lightmap"
import { ObjectBase } from "../../../engine"

/**
 *
 */
export class Flashlight extends ObjectBase {
  constructor(resources: IResourceDictionary) {
    super(resources)

    const texture = Texture.from("flashlight.png")
    const lightCone = new Sprite(texture)

    this.scale.set(2)
    lightCone.width = 100
    lightCone.height = 50
    lightCone.alpha = 0.2
    lightCone.tint = 0xefebd8
    lightCone.y = -20

    lightCone.zIndex = 99999
    const f = new SimpleLightmapFilter(texture)

    f.alpha = 0.01
    f.blendMode = BLEND_MODES.ADD
    lightCone.filters = [new filters.NoiseFilter(0.2), f]

    this.zIndex = 9999
    this.addChild(lightCone)
  }
}
