import {
  Text,
  Container,
  Sprite,
  IResourceDictionary
} from "pixi.js"
import { Config } from "../config"
import { HUDBase, PlayerBase } from "../../engine"

export class HUD extends HUDBase {
  private readonly _health: Text
  // round icons
  private readonly _rounds: Container
  // weapon icon
  private _weapon?: Sprite

  constructor(
    resources: IResourceDictionary,
    player: PlayerBase
  ) {
    super(resources)

    const sheet = "hud"
    const resource = resources[sheet]

    console.log(
      "Setting up HUD",
      "width:",
      Config.constants.width,
      "height:",
      Config.constants.height
    )

    this._player = player

    const smallTextConfig = {
      ...Config.styling.bigText,
      fontSize: "12px"
    }

    {
      const playerName = new Text(player.playerName, {
        ...smallTextConfig,
        fill: "#ffff30"
      })
      playerName.x =
        Config.constants.width / 3 - playerName.width / 2
      playerName.y = Config.constants.height - 47
      this.addChild(playerName)

      this._health = new Text("", smallTextConfig)
      this._health.x = playerName.x
      this._health.y = Config.constants.height - 23
      this.addChild(this._health)
    }

    this._rounds = new Container()
    this._rounds.x = Config.constants.width / 3 + 77
    this._rounds.y = Config.constants.height - 33
    this.addChild(this._rounds)

    {
      const healthBorder = new Sprite(
        resource.textures!["side.png"]
      )

      healthBorder.x =
        Config.constants.width / 2 -
        (healthBorder.width + healthBorder.width / 2) +
        2
      healthBorder.y =
        Config.constants.height - healthBorder.height - 2

      this.addChild(healthBorder)
    }

    {
      const roundsBorder = new Sprite(
        resource.textures!["center.png"]
      )
      roundsBorder.x =
        Config.constants.width / 2 -
        roundsBorder.width / 2 +
        1
      roundsBorder.y =
        Config.constants.height - roundsBorder.height - 2

      this.addChild(roundsBorder)
    }

    {
      const weaponBorder = new Sprite(
        resource.textures!["side.png"]
      )

      weaponBorder.x =
        Config.constants.width / 2 +
        (weaponBorder.width + weaponBorder.width / 2) -
        2
      weaponBorder.y =
        Config.constants.height - weaponBorder.height - 2

      weaponBorder.scale.x = -1

      this.addChild(weaponBorder)
    }

    this.update()
  }

  private _renderWeapon() {
    this.removeChild(this._weapon!)

    this._weapon = new Sprite(
      this._player.weapon.sprite!.texture
    )
    this._weapon.scale.x =
      this._player.weapon.sprite!.scale.x * 3
    this._weapon.scale.y =
      this._player.weapon.sprite!.scale.y * 3

    this._weapon.x =
      Config.constants.width / 2 + (this._weapon.width + 50)
    this._weapon.y = Config.constants.height - 60

    this.addChild(this._weapon)
  }

  private _renderRounds() {
    this._rounds.removeChildren()

    for (let x = 0; x < this._player.weapon.rounds; x++) {
      const roundSprite = new Sprite(
        this._player.weapon.roundsSprite?.texture
      )
      roundSprite.scale.x = 0.25
      roundSprite.scale.y = 0.25
      roundSprite.x = 12 * x
      roundSprite.y = 10

      this._rounds.addChild(roundSprite)
    }
  }

  public update() {
    super.update()

    this._health.text = this._player.health + " HP"

    this._renderRounds()
    this._renderWeapon()
  }

  public tick(delta: number) {
    if (this._gameOverSign) {
      this._gameOverSign.alpha += delta / 1000
      if (this._gameOverSign.alpha > 1) {
        this._gameOverSign.alpha = 0.2
      }
    }
  }
}
