import {
  AnimatedSprite,
  IResourceDictionary
} from "pixi.js"

/**
 * @class
 */
export class AnimationHelper {
  static loadClip(
    resources: IResourceDictionary,
    sheet: string,
    animation: string,
    speed: number,
    scale: number
  ): AnimatedSprite {
    if (
      !resources[sheet] ||
      !resources[sheet].spritesheet
    ) {
      throw new Error(`spritesheet ${sheet} not found!`)
    }

    if (
      !resources[sheet].spritesheet!.animations[animation]
    ) {
      throw new Error(`Animation ${animation} not found|`)
    }

    const clip = new AnimatedSprite(
      resources[sheet].spritesheet!.animations[animation]
    )

    clip.animationSpeed = speed
    clip.scale.x = scale
    clip.scale.y = scale
    clip.anchor.x = 0.5

    return clip
  }
}
