import { IResourceDictionary, filters } from "pixi.js"
import {
  AnimationBase,
  AnimationHelper
} from "../../engine"

/**
 *
 */
export class Door extends AnimationBase {
  constructor(
    resources: IResourceDictionary,
    done?: () => void
  ) {
    super(resources)

    const sheet = "things"

    this._clips = {
      [sheet]: AnimationHelper.loadClip(
        this._resources,
        sheet,
        "door",
        0.01,
        8
      )
    }

    this._clips[sheet].filters = [
      new filters.BlurFilter(0.2)
    ]

    this._playClip(sheet, false, false).then(() => {
      if (done) {
        done()
      }
    })
  }
}
