import {
  IResourceDictionary,
  Sprite,
  Graphics,
  Text,
  TextStyle
} from "pixi.js"
import { Config } from "../config"
import { AnimationBase } from "../../engine"

export class Dialogbox extends AnimationBase {
  _text: string[]
  _textObject

  constructor(
    resources: IResourceDictionary,
    text: string[] = [
      "Hahhahaha, kid!",
      "You will never stop me",
      "This is the end ... "
    ]
  ) {
    super(resources)

    this._text = text.reverse()

    const avatarTexture =
      resources["avatar.dejonghe"].texture
    const avatar = new Sprite(avatarTexture)
    avatar.height = 70
    avatar.width = 70
    avatar.y = 2
    avatar.x = 2

    const border = new Graphics()
    border.lineStyle(4, 0x111111)
    border.drawRect(0, 0, 400, 72)

    const style: TextStyle = {
      fontSize: "14px",
      fill: "#ccc",
      fontFamily: Config.styling.fontFamily,
      dropShadow: true,
      dropShadowColor: "#222"
    } as TextStyle

    this._textObject = new Text("", style)
    this._textObject.x = 80
    this._textObject.y = 12

    this.addChild(border, avatar, this._textObject)
  }

  public start() {
    setTimeout(() => {
      this.play()
    }, Config.constants.initialDelay)
  }

  private printLine(line: string) {
    return new Promise((resolve) => {
      const text = line.split("").reverse()

      const lineInterval = setInterval(() => {
        const char = text.pop()
        if (char) {
          this._textObject.text += char
          if (char !== " ") {
            const sound = this._resources["sfx.keypress"]
              .sound
            sound.play({
              start: 0.7,
              speed: 3.0,
              volume: 0.2
            })
          }
        } else {
          clearInterval(lineInterval)
          return resolve()
        }
      }, Config.constants.letterDelay)
    })
  }

  private play() {
    const line = this._text.pop()
    if (line) {
      this.printLine(line).then(() => {
        if (this._text.length > 0) {
          setTimeout(() => {
            this._textObject.text += "\n"
            this.play()
          }, Config.constants.lineDelay)
        } else {
          setTimeout(() => {
            if (this._onDone) {
              return this.done()
            }
          }, Config.constants.lineDelay)
        }
      })
    }
  }
}
