import { ParticleContainer, Texture } from "pixi.js"
import {
  EmitterConfig,
  OldEmitterConfig
} from "pixi-particles"
import { ITickable } from "../interfaces"
import { particles } from "../tools/pixi"

/**
 *
 */
export class ParticleBase
  extends ParticleContainer
  implements ITickable {
  private emitter

  constructor(
    images: Texture[],
    config: EmitterConfig | OldEmitterConfig
  ) {
    super()

    this.emitter = new particles.Emitter(
      // The PIXI.Container to put the emitter in
      // if using blend modes, it's important to put this
      // on top of a bitmap, and not use the root stage Container
      this,

      // The collection of particle images to use
      images,

      // Emitter configuration, edit this to change the look
      // of the emitter
      config
    )

    // Start emitting
    this.emitter.emit = true
  }

  public tick(delta: number) {
    this.emitter.update(delta * 0.001)
  }
}
