import { IResourceDictionary, Sprite } from "pixi.js"
import Weapon from "./weapon"

/**
 *
 */
export class Beretta extends Weapon {
  fireSound
  reloadSound

  /**
   *
   * @param resources
   */
  constructor(resources: IResourceDictionary) {
    super(resources, 55, 8)

    this.fireSound = resources["beretta.fire"].sound
    this.reloadSound = resources["beretta.reload"].sound

    this._reloadTime = this.reloadSound.duration

    const resource = resources!["weapons.beretta"]

    this._sprite = new Sprite(
      resource.textures!["beretta.png"]!
    )

    this._sprite.scale.set(0.25, 0.25)

    // sprite should be in the back so add it first
    this.addChild(this._sprite)

    this._roundsSprite = new Sprite(
      resource.textures!["beretta_shell.png"]!
    )
  }

  public fire(): boolean {
    const f = super.fire()
    if (f) {
      this.fireSound.play()
    }
    return f
  }

  reload(): boolean {
    const didReload = super.reload()
    if (didReload) {
      this.reloadSound.play()
    }
    return didReload
  }
}
