import { Container, IResourceDictionary } from "pixi.js"
import { Dialogbox } from "../objects"
import { Config } from "../config"
import { MapBase } from "../../engine"

/**
 *
 */
export class CutScene extends MapBase {
  dialogBox

  constructor(
    resources: IResourceDictionary,
    stage: Container
  ) {
    super(resources, stage, "maps.maension")

    this.withHud = false
    this.withCrosshair = false
    this.withPlayer = false
    this.withMouse = false

    this.dialogBox = new Dialogbox(resources)

    this.dialogBox.x =
      Config.constants.width / 2 - this.dialogBox.width / 2
    this.dialogBox.y =
      Config.constants.height / 3 -
      this.dialogBox.height / 2

    this.foregroundContainer.addChild(this.dialogBox)

    this.dialogBox.start()
    this.dialogBox.onDone = () => this.done()
  }

  protected done() {
    this.fade().then(() => {
      super.done()
    })
  }

  public tick(delta: number) {}
}
