import {
  Container,
  IResourceDictionary,
  Sprite,
  Texture,
  BLEND_MODES
} from "pixi.js"
import {
  Keyboard,
  MapBase,
  TextWithBackground
} from "../../engine"
import { Config } from "../config"
import { GodrayFilter } from "@pixi/filter-godray"
import { Lightning } from "../objects"
import { RainFilter } from "../filters/rain"
import { easing, tween } from "popmotion"

/**
 *
 */
export class Title extends MapBase {
  constructor(
    resources: IResourceDictionary,
    stage: Container
  ) {
    super(resources, stage, "maps.maension")

    this._bgm = resources["bgm.title"].sound
    this._bgm.volume = 0.3
    this._bgm.loop = true
    this._bgm.play()

    this.withHud = false
    this.withCrosshair = false
    this.withPlayer = false
    this.withMouse = false

    const brand = new TextWithBackground(
      resources,
      "Mnsion",
      "54px",
      "#EFB775",
      undefined,
      "#111"
    )
    brand.x = Config.constants.width / 2 - brand.width / 2
    brand.y = Config.constants.height / 3 - brand.height / 2

    const isInteractive =
      window.location.search.endsWith("interactive") ||
      !Config.lockTitleScreen

    let pressStart = "Press Enter to play"
    if (!isInteractive) {
      pressStart = "coming soon ..."
    }

    const enterText = new TextWithBackground(
      resources,
      pressStart,
      "24px"
    )

    tween({
      from: 1.0,
      to: 0.2,
      loop: Infinity,
      duration: 1500,
      ease: [easing.easeInOut]
    }).start((v: number) => (enterText.alpha = v))

    enterText.x =
      Config.constants.width / 2 - enterText.width / 2
    enterText.y =
      Config.constants.height / 2 - enterText.height / 2

    const maension = new Sprite(
      this._textures!["Maension_WIP.png"]
    )

    maension.alpha = 0.6
    const scale = 2
    maension.scale.x = scale
    maension.scale.y = scale

    maension.y = Config.constants.height - maension.height
    maension.x =
      Config.constants.width / 2 - maension.width / 2

    this._foregroundContainer.addChild(
      new Lightning(),
      maension,
      enterText,
      brand
    )

    {
      const god = new Sprite(Texture.WHITE)

      god.width = Config.constants.width
      god.height = Config.constants.height
      god.tint = Config.constants.black

      this.godFilter = new GodrayFilter()
      this.godFilter.angle = -35
      this.godFilter.gain = 0.5
      this.godFilter.lacunarity = 2.5
      god.filters = [this.godFilter]

      this._backgroundContainer.addChild(god)
    }

    {
      const rain = new Sprite(Texture.WHITE)

      rain.width = Config.constants.width
      rain.height = Config.constants.height
      rain.tint = Config.constants.black

      this.rainFilter = new RainFilter(resources)
      this.rainFilter.uniforms.dimensions = [
        Config.constants.width,
        Config.constants.height
      ]
      this.rainFilter.uniforms.time = 1
      this.rainFilter.uniforms.u_mouse = [
        Config.constants.width,
        Config.constants.height
      ]
      this.rainFilter.uniforms.u_frame = 0
      this.rainFilter.blendMode = BLEND_MODES.ADD

      rain.filters = [this.rainFilter]

      this._backgroundContainer.addChild(rain)
    }

    this._foregroundContainer.interactive = true

    if (isInteractive) {
      this.wireEvents()
    }
  }

  private wireEvents() {
    const enter = new Keyboard("Enter", true)
    enter.pressEvent = () => this.done()

    this._foregroundContainer.on(
      "touchstart",
      this.done.bind(this)
    )
  }

  protected done() {
    Promise.all([this.fade(), this.fadeMusic()]).then(
      () => {
        super.done()
      }
    )
  }

  rainFilter
  godFilter

  count = 0

  public tick(delta: number) {
    this.count += delta * 0.001
    this.godFilter.time = this.count
    this.rainFilter.uniforms.time = this.count
  }
}
